<template>
  <div>
    <!-- Text display -->
    <FormulateInput
      type="text"
      placeholder="Type it here"
      label="Goal description"
      required="true"
      name="title"
      v-model="goal.description"
    />
    <!--Number of battles-->
    <FormulateInput
      type="number"
      v-model.number="g.battlesWon"
      label="How many battles should be won"
    />
    <!--If they should be in a row-->
    <FormulateInput type="checkbox" label="In a row" v-model="goal.inRow" />

    <FormulateInput
      type="select"
      v-model.number="goal.against"
      label="vs Character (set to None if no specific target)"
      :options="charIds"
    />

    <div>
      <FormulateInput
        type="select"
        v-model.number="goal.againstGroup"
        label="vs Character (set to None if no specific target)"
        :options="charGroups"
      />
      <button @click.prevent="goal.againstGroup=undefined">reset</button>
    </div>

    <FormulateInput
      type="select"
      v-model.number="goal.with"
      label="with Character (set to None if no specific target)"
      :options="charIds"
    />
  </div>
</template>

<script>
export default {
  props: ["goal", "charIds"],
  computed: {
    g() {
      return this.goal;
    },
    charGroups() {
      return this.$store.getters["enums/getEnum"]("pokemonTypings");
    },
  },
  created() {
    this.$set(this.charIds, -1, "None");
  },
};
</script>

